*{
    box-sizing: border-box;
    padding: 0%;
    margin: 0%;
}
.Madrese-bg{
    padding-top: 15%;
    height: 140vh;
    background-image: url('./image/Madresebg.svg');
    background-repeat: no-repeat;
    background-position-x: 350px;
    background-position-y: -100px;
    background-size: cover;
}
.Madrese-content{
    display: flex;
    justify-content: space-between;
    flex-direction: row-reverse;
    margin-right: 5%;
    margin-left: 5%;
    direction: rtl;
}
.Madrese-content-texts{
    display: flex;
    flex-direction: column;
    width: 100%;
    padding-top: 7%;
    align-items: center;
}
.Madrese-content-texts h1{
    color: #003366;
    font-size: 40px;
    text-align: center;
    font-weight: bold;
    width: 70%;
}
.Madrese-content-texts p{
    text-align: center;
    color: #3D3D3D;
    padding-top: 10%;
    font-size: 18px;
    line-height: 30.13px;
}
.Madrese-content img{
    width: 50%;
    height: 546px;
}

/* content 2 */
.Madrese-content2{
    display: flex;
    justify-content: space-between;
}
.Madrese-content2 img{
    width: 30%;
    height: auto;
}
.Madrese-content2-texts{
    display: flex;
    flex-direction: column;
    direction: rtl;
    padding-top: 10%;
    align-items: center;
    width: 70%;
}
.Madrese-content2-texts p{
    color: #000000;
    text-align: center;
    font-size: 24px;
}
.Madrese-content2-texts span{
    padding-top: 5%;
    color: #FF7F50;
    font-size: 24px;
    display: flex;
    cursor: pointer;
    align-items: center;
}
.Madrese-content2-texts span img{
    width: 40px;
    height: 40px;
    padding-top: 1%;
    padding-right: 1%;
}
.Madrese-content2-lists{
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    padding-top: 2%;
    width: 40%;
}
.Madrese-content2-lists a{
    direction: rtl;
    color: #C2C7D6;
    margin-top: 15%;
    padding-right: 5%;
    cursor: pointer;
    font-size: 24px;
    font-family: "Interr","Sans";
    font-weight: bold;
}
.Madrese-content2-lists .Madrese-active-list{
    color: #194775;
    font-family: "Interr","Sans";
    border-right: 4px solid #003366;
}
/* sabt darkhast */
.Madrese-sabt-darkhast{
    margin-top: 10%;
    margin-bottom: 10%;
    height: 40vh;
    width: 100%;
    background-image: url('./image/Madresebg2.svg');
    background-repeat: no-repeat;
    background-position-y: bottom;
    background-size: cover;
    display: flex;
    direction: rtl;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.Madrese-sabt-darkhast h1{
    color: #003366;
    font-size: 40px;
    font-weight: bold;
}
.Madrese-sabt-darkhast button{
    background-color: #FF7F50;
    color: #FFFFFF;
    padding: 1%;
    border: none;
    border-radius: 4px;
    font-size: 20px;
    margin-top: 5%;
    cursor: pointer;
    transition: .3s;
    width: 30%;
}
.Madrese-sabt-darkhast button:hover{
    transition: .3s;
    background-color: #ce400c;
}
/* madrese road map */
.Madrese-roadmap-header{
    text-align: center;
    color: #FF7F50;
    font-size: 40px;
    margin-bottom: 10%;
}
.Madrese-roadmap-box{
    display: flex;
    justify-content: space-between;
    width: 100%;
    height: auto;
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
    padding-bottom: 5%;
}
.Madrese-roadmap-box img{
    width: 35% !important;
}
.Madrese-roadmap-box-texts{
    display: flex;
    direction: rtl;
    flex-direction: column;
    align-items: center;
    padding-right: 5%;
}
.Madrese-roadmap-box-texts h1{
    padding-top: 7%;
    font-family: "Interr","Sans";
    font-size: 32px;
    color: #194775;
    font-weight: bold;
}
.Madrese-roadmap-box-texts p{
    text-align: justify;
    padding-top: 10%;
    font-size: 24px;
    color: #000000;
}
/* box 2 */
.Madrese-roadmap-box2{
    display: flex;
    justify-content: space-between;
    flex-direction: row-reverse;
    width: 100%;
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
    height: auto;
    padding-bottom: 5%;
}
.Madrese-roadmap-box2 img{
    width: 35% !important;
}
.Madrese-roadmap-box-texts2{
    display: flex;
    direction: rtl;
    flex-direction: column;
    align-items: center;
    padding-left: 5%;
}
.Madrese-roadmap-box-texts2 h1{
    padding-top: 7%;
    font-family: "Interr","Sans";
    font-size: 32px;
    color: #194775;
    font-weight: bold;
}
.Madrese-roadmap-box-texts2 p{
    text-align: justify;
    padding-top: 10%;
    font-size: 24px;
    color: #000000;
}
.Madrese-slider{
    margin-top: 5%;
    margin-right: 9%;
    margin-left: 9%;
}
.Madrese-roadmap-box img{
    width: 60%;
}
.Madrese-roadmap-box2 img{
    width: 60%;
}
/* responsive ---------- */
@media only screen and (max-width: 1500px) {
    .Madrese-roadmap-box-texts2 p{
        font-size: 20px;
    }
    .Madrese-roadmap-box-texts p{
        font-size: 20px;
    }
    .Madrese-content2-texts p{
        font-size: 20px;
    }
    .Madrese-content2-lists a{
        font-size: 22px;
    }
}
@media only screen and (max-width: 1200px) {
    .Madrese-roadmap-box-texts2 p{
        font-size: 18px;
    }
    .Madrese-roadmap-box-texts p{
        font-size: 18px;
    }
    .Madrese-content2-texts p{
        font-size: 18px;
    }
    .Madrese-content2-lists a{
        font-size: 20px;
    }
    .Madrese-content-texts h1{
        font-size: 33px;
    }
    .Madrese-content-texts p{
        font-size: 16px;
    }
}
@media only screen and (max-width: 1000px) {
    .Madrese-roadmap-box-texts2 p{
        font-size: 16px;
    }
    .Madrese-roadmap-box-texts p{
        font-size: 16px;
    }
    .Madrese-content2-texts p{
        font-size: 16px;
    }
    .Madrese-content2-lists a{
        font-size: 18px;
    }
}
/* mobile */
@media only screen and (max-width: 450px) {
    .Madrese-bg{
        background-position-x: -40px;
        background-position-y: 20%;
        height: 80vh;
    }
    .Madrese-content{
        flex-direction: column-reverse;
    }
    .Madrese-content img{
        width: 100%;
        height: 35vh;
        margin-top: 15%;
    }
    .Madrese-content-texts h1{
        font-size: 27px;
        font-weight: 700;
        width: 80%;
    }
    .Madrese-content-texts p{
        font-size: 18px;
        text-align: justify;
        width: 90%;
    }
    .Madrese-content2{
        display: none;
    }
    .Madrese-sabt-darkhast{
        margin-top: 35%;
        height: 25vh;
    }
    .Madrese-sabt-darkhast h1{
        font-size: 21px;
        font-weight: 700;
        margin-top: 1%;
    }
    .Madrese-sabt-darkhast button{
        font-size: 17px;
        width: 60%;
        margin-top: 10%;
        padding: 3%;
    }
    .Madrese-roadmap-header{
        font-size: 20px;
        font-weight: 700;
    }
    .Madrese-roadmap-box{
        flex-direction: column;
        align-items: center;
        height: auto;
        padding-bottom: 5%;
    }
    .Madrese-roadmap-box-texts{
        padding-right: 0;
        margin-right: 5%;
        margin-left: 5%;
    }
    .Madrese-roadmap-box-texts h1{
        font-size: 32px;
        font-weight: 700;
    }
    .Madrese-roadmap-box-texts p{
        font-size: 22px;
        font-weight: 500;
    }
    /* box 2 */
    .Madrese-roadmap-box2{
        flex-direction: column;
        align-items: center;
        height: auto;
        margin-top: 5%;
    }
    .Madrese-roadmap-box-texts2{
        padding-right: 0;
        margin-right: 5%;
        padding-bottom: 5%;
    }
    .Madrese-roadmap-box-texts2 h1{
        font-size: 32px;
        font-weight: 700;
    }
    .Madrese-roadmap-box-texts2 p{
        font-size: 22px;
        font-weight: 500;
    }
}